import React from "react";
import classSet from "classnames";
import { EditorWidget } from "webiny-app-cms";

import RowWidgetCmp from "./components/Widget";
import RowWidgetSettingsCmp from "./components/Settings";
import styles from "./styles.scss?prefix=Webiny_CMS_Row_Widget";

class RowWidget extends EditorWidget {
    constructor(config) {
        super();
        this.config = config;
    }

    renderSelector() {
        return (
            <span className={styles.link}>
                {this.config.layout.map((grid, index) => {
                    return <span key={index} className={classSet(styles.layout, styles["grid" + grid])} />;
                })}
                <span className={styles.txt}>{this.config.title}</span>
            </span>
        );
    }

    renderWidget({ WidgetContainer }) {
        return (
            <WidgetContainer>
                <RowWidgetCmp layout={this.config.layout} />
            </WidgetContainer>
        );
    }

    renderSettings({ WidgetSettingsContainer }) {
        return (
            <WidgetSettingsContainer>
                <RowWidgetSettingsCmp />
            </WidgetSettingsContainer>
        );
    }
}

export default RowWidget;
